* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  text-decoration: none;
  text-underline: none;
}

.app {
  position: relative;

  width: 100vw;
  height: 100vh;

  overflow-x: hidden;
  overflow-y: scroll;

  .appContent {
    position: absolute;
    height: 100%;
    right: 200px;
    width: calc(100% - 250px*2);

    @media (max-width: 1400px) {
      position: relative;
      width: calc(100% - 76px);
      left: 76px;
      padding: 0 15px;
    }
  }
}

// Scrollbar custom styles
::-webkit-scrollbar {
  width: 9px;
  background-color: lightgray;
  position: absolute;
  right: 0;
}


::-webkit-scrollbar-button {
  //background-color: #ef7f1a;
  //display: none;
}

::-webkit-scrollbar-track {
  /* 3 - трек */
  background: transparent;
  display: none;
}

::-webkit-scrollbar-track-piece {
  /* 4 - видимая часть трека */
}

::-webkit-scrollbar-thumb {
  /* 5 - ползунок */
  border-radius: 12px;
  background-color: #aaa;
}

::-webkit-scrollbar-corner {
  /* 6 - уголок */
}

::-webkit-resizer {
